.App {
  text-align: center;
  background-color: #f9d29d;
  background-image: linear-gradient(315deg, #f9d29d 0%, #ffd8cb 74%);

  
}

.Main {

  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center; 
  flex-direction: column;
}
.input {

  width: 300px;
  height: 25px;
  border: 2px solid black;
}
.Content {
   
  display: flex;
  flex-direction: row ;
}
.keys {

      border: 2px solid black;
      width: 305px;
      position: relative;
      right: 42px;
      margin: 0%;
      color:  black;
      background-color:lightgrey;
      cursor: pointer;
       
}
.keys:hover {

       font-size: large;   

}
.button {

   margin-left: 10px ;
   padding: 5px;
   border-radius: 10px;
   border: 3px solid sienna;
   background-color: teal;

}
.button:hover {

  background-color: rgb(0, 51, 128);
      

}

